import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import soundfile from './tin1.mp3';


class Counter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      countNum:0,
      play334:false,
    };
  }
  render(){
    return(
      <div>
        <h1>なんでや！阪神関係ないやろ！</h1>
        <p>{this.state.countNum*33}-{this.state.countNum*4}</p>
        <button onClick={this.proc}>Do 334</button>
      </div>
      
    );
  }
  proc = () => {
    this.countUP();
    this.play();    //２つの関数を実行
  }
  countUP = () =>{
    this.setState({countNum:this.state.countNum+1});
  }
  play = () =>{
    this.audio=new Audio(soundfile);
    this.audio.play();
  }
}

ReactDOM.render(
  <Counter />,
  document.getElementById('root')
);
